'use strict';

// MODULES //

import isPlainObject from '@stdlib/assert-is-plain-object' ;
import hasOwnProp from '@stdlib/assert-has-own-property' ;
import { isPrimitive as isNumber } from '@stdlib/assert-is-number' ;
import { isPrimitive as isInteger } from '@stdlib/assert-is-integer' ;
import { isPrimitive as isPositiveInteger } from '@stdlib/assert-is-positive-integer' ;
import { isPrimitive as isNonNegativeInteger } from '@stdlib/assert-is-nonnegative-integer' ;
import isEven from '@stdlib/math-base-assert-is-even' ;
import isnan from '@stdlib/math-base-assert-is-nan' ;
import format from '@stdlib/error-tools-fmtprodmsg' ;


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination object
* @param {Options} options - function options
* @param {PositiveInteger} [options.period] - number of iterations before a square wave repeats
* @param {number} [options.min] - minimum amplitude
* @param {number} [options.max] - maximum amplitude
* @param {integer} [options.offset] - phase offset (in units of iterations)
* @param {NonNegativeInteger} [options.iter] - number of iterations
* @returns {(Error|null)} null or an error object
*
* @example
* var opts = {};
* var options = {
*     'iter': 100
* };
* var err = validate( opts, options );
* if ( err ) {
*     throw err;
* }
*/
function validate( opts, options ) {
	if ( !isPlainObject( options ) ) {
		return new TypeError( format( '0Rg2h', options ) );
	}
	if ( hasOwnProp( options, 'period' ) ) {
		opts.period = options.period;
		if ( !isPositiveInteger( options.period ) || !isEven( options.period ) ) { // eslint-disable-line max-len
			return new TypeError( format( '0Rg8i', 'period', options.period ) );
		}
	}
	if ( hasOwnProp( options, 'min' ) ) {
		opts.min = options.min;
		if ( !isNumber( options.min ) || isnan( options.min ) ) {
			return new TypeError( format( '0Rg8h', 'min', options.min ) );
		}
	}
	if ( hasOwnProp( options, 'max' ) ) {
		opts.max = options.max;
		if ( !isNumber( options.max ) || isnan( options.max ) ) {
			return new TypeError( format( '0Rg8h', 'max', options.max ) );
		}
	}
	if ( hasOwnProp( options, 'offset' ) ) {
		opts.offset = options.offset;
		if ( !isInteger( options.offset ) ) {
			return new TypeError( format( '0Rg8e', 'offset', options.offset ) );
		}
	}
	if ( hasOwnProp( options, 'iter' ) ) {
		opts.iter = options.iter;
		if ( !isNonNegativeInteger( options.iter ) ) {
			return new TypeError( format( '0Rg35', 'iter', options.iter ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
